<template>

  <div class="archives">
    <div class="archives-top">
      <span class="archives-top-icon">档案</span>
      <i class="iconfont icon-shezhi archives-top-icon"></i>
      <div class="archives-top-container">
        <img src="http://imgqn.koudaitong.com/upload_files/2015/06/08/143376837234596171.jpg%21580x580.jpg">
        TAO PAN
      </div>
      <button><i class="iconfont icon-QRcode"></i> NIKEPLUS通行证</button>
    </div>
    <nav-head :items="carouselFigureArray" @nav-head-click="handleNavHeadClick"></nav-head>
    <div class="archives-bottom">
      <div class="archives-bottom-collection" v-show="carouselFigurePos === 1">
        <shoes-card v-for="(item,index) in shoesArray" :key="index" :shoes="item" :shoesSize="shoesSize"
                    :showInfo="false"
                    @shoes-click="handleOpenShoesDetail"></shoes-card>
      </div>
      <div class="archives-bottom-purchased" v-show="carouselFigurePos === 2">
        <shoes-card-list :list="item" v-for="(item,index) in shoesOwnArray" :key="index"
                         @shoes-list-click="handleOpenShoesDetail"></shoes-card-list>
      </div>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  import NavHead from '../../components/NavHead/NavHead.vue';
  import ShoesCard from '../../components/ShoesCard/ShoesCard.vue';
  import ShoesCardList from '../../components/ShoesCardList/ShoesCardList.vue';

  export default{
    components: {
      NavHead, ShoesCard, ShoesCardList
    },
    props: {},
    computed: {},
    data(){
      return {
        carouselFigureArray: [
          {
            id: 1,
            title: '收藏夹',
            active: true
          },
          {
            id: 2,
            title: '已购买',
            active: false
          }
        ],
        carouselFigurePos: 1
        ,
        shoesArray: [],
        shoesOwnArray: [],
        shoesSize: document.body.clientWidth / 2 - 4
      }
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
      let _this = this;
      _this.axios.get('/getPurchase?user_id=u_1&type=0').then((response) => {
        if (response.data.code === 0) {
          let purchase = response.data.result.purchase;
          for (let i = 0; i < purchase.length; i++) {
            _this.shoesArray.push(purchase[i].shoes);
          }
        }
      });
      _this.axios.get('/getPurchase?user_id=u_1&type=1').then((response) => {
        if (response.data.code === 0) {
          let purchase = response.data.result.purchase;
          for (let i = 0; i < purchase.length; i++) {
            _this.shoesOwnArray.push(purchase[i].shoes);
          }
        }
      })
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    },
    methods: {
      handleNavHeadClick(data){
        let _this = this;
        this.carouselFigurePos = data.target;
        for (let i = 0, length = this.carouselFigureArray.length; i < length; i++) {
          this.carouselFigureArray[i].active = data.target === this.carouselFigureArray[i].id;
        }
      },
      handleOpenShoesDetail(data){
        let _this = this;
        this.axios.get('/getShoes?s_id=' + data.target).then((response) => {
          if (response.data.code === 0) {
            let detail = {
              basic: response.data.result.shoes,
              images: response.data.result.images
            };
            _this.$store.commit({
              type: 'moduleShoes/updateShoesDetail',
              shoesDetail: detail
            });
            _this.$store.commit({
              type: 'moduleShoes/shoesDetailShowOrClose'
            });
          }
        })
      }
    }
  }
</script>
<style scoped lang="scss">
  $viewName: 'archives';
  .#{$viewName} {
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 60px;
  }

  .#{$viewName}-top {
    width: 100%;
    height: 55%;
    /*background: whitesmoke;*/
    position: relative;
    &-icon {
      font-size: 22px;
      font-weight: bold;
      position: absolute;
      top: 0;
    }
    & span {
      left: 0;
      transform: translate(50%, 50%);
    }
    & .icon-shezhi {
      right: 0;
      transform: translate(-50%, 50%);
    }
    & button {
      position: absolute;
      bottom: 0;
      left: 50%;
      width: 160px;
      height: 48px;
      padding: 8px;
      border: solid 1px lightgray;
      border-radius: 4px;
      font-size: 14px;
      font-weight: bold;
      background: white;
      transform: translate(-50%, -60%);
      & .icon-QRcode {
        display: inline-block;
        font-size: 12px;
        transform: scale(.8);
      }
    }

    &-container {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      font-size: 22px;
      font-weight: bold;
      & img {
        width: 100px;
        height: 100px;
        display: block;
        margin: 14px auto;
        border-radius: 50%;
      }
    }
  }

  .#{$viewName}-bottom {
    width: 100%;
    &-collection {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    &-purchased {
      position: relative;
      overflow-y: auto;
    }
  }

</style>



