<template>

  <div class="shoes-card-list-content" @click="shoesCardListClick">
    <div class="shoes-card-list-content-left">
      <img class="shoes-card-list-content-left-img" :src="list.img">
    </div>
    <div class="shoes-card-list-content-right">
      <span>{{list.name}}</span>
      <span>{{list.time}}</span>
      <span>{{list.price}}</span>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  export default{
    components: {},
    props: {
      list: {
        type: Object,
        default: {}
      }
    },
    computed: {},
    data(){
      return {}
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    },
    methods: {
      shoesCardListClick(){
        let _this = this;
        let target = _this.list.id;
        _this.$emit('shoes-list-click', {
          target: target
        })
      }
    }

  }
</script>
<style scoped lang="scss">
  $className: 'shoes-card-list';
  .#{$className}-content {
    width: 100%;
    height: 120px;
    position: relative;
    margin-top: 16px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    & div {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &-left {
      width: 40%;
      left: 0;
      & img {
        width: 80%;
      }
    }
    &-right {
      width: 60%;
      right: 0;
      flex-direction: column;

    }
  }
</style>



